h1{
  color: black;
}

/* Home Page */
.home-container{
  height: 90vh;
  width: 100%;
  display: flex;
  justify-content: space-around;
  background: #FFFFFF;
  background: linear-gradient(to bottom right, #FFFFFF, #E3E3E3);
}

.left-row{
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 10% 10%;
  margin-top: 15rem;
}

.name-style{
  color: #255384;
  font-size: 3rem;
}

.name-title{
  font-size: 2rem;
  padding-bottom: 10px;
  padding-top: 10px;
}

.about-links{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-btn{
  text-decoration: none;
  border: 2px solid black;
  background-color: white;
  color: black;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
}

.linkedIN:hover{
  text-decoration: none;
  background-color: #2867B2;
  color: #FFFFFF;
}

.github{
  margin-right: 8rem;
}

.github:hover{
  text-decoration: none;
  background-color: #6f42c1;
  color: #FFFFFF;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
  border-radius: 90%;
  margin-top: 8.5rem;
}


/* About Page */

.about-container{
  height: 90vh;
  display: grid;
  grid-template-rows: 20% 30% 20%;
  padding: 2rem 3rem;
  align-items: center;
  justify-content: center;
  background: #FFFFFF;
  background: linear-gradient(to bottom right, #FFFFFF, #E3E3E3);
}

.about-heading{
  font-size: 3rem;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-weight: 1000;
}

.about-description{
  font-size: 3rem;
  margin-top: 10rem;
  margin-bottom: 5rem;
  line-height: 50px;
}

.resume-button{
  text-decoration: none;
  margin-top: 8rem;
  width: 9%;
  color: white;
  align-self: center;
  border: 2px solid black;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
  background: linear-gradient(to left, #0E8DF2, #0809AA);
  cursor: pointer;
}

.resume-button:hover{
  background: #0e97f2;
  color: black;
  text-decoration: none;
}

/* Projects Page*/

.project-container{
  height: 80vh;
  display: flex;
  padding: 2rem 3rem;
  align-items: center;
  justify-content: center;
  background: #FFFFFF;
  background: linear-gradient(to bottom right, #FFFFFF, #E3E3E3);
}

.img-project{
  height: 50%;
  width: 50%;
}

/* Contact Page */

.contact-text{
  font-size: 4rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-container{
  padding: 2rem 3rem;
  height: 90vh;
}

.success-msg{
  color: #57A773;
  padding: 10px 15px;
}

.err-msg{
  color: #EE6352;
  padding: 10px 15px;
}

/* Navbar */

.navBar{
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #282c34;
  color: white;
  outline: none;
}

.nav-links{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 50%;
  outline: none;
  color: white;
  text-decoration: none;
}

.links{
  text-decoration: none;
  color: white;
}

.links:hover{
  color: #fff;
  border-bottom: 3px solid #0D7CFF;;
}

/* Animations */

.text-animation .name-title{
  margin: 0px;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 3s steps(20,end) forwards,
  blink 1s infinite;
}

@keyframes typewriter{
  from {
    width: 0;
  }
  to {
    width: 16em;
  }
}

@keyframes typing {
  0% { width: 0% }
  100% { width: 100% }
}
@keyframes blink {
  0%,100% {
    border-right: 2px solid transparent;
  }
  50% {
    border-right: 2px solid #222;
  }
}
